import { styled } from "styled-components";
import StackLists from "./StackLists";
//  
import html5 from "assets/html5.png";
import css3 from "assets/css3.png";
import js from "../../assets/js.png";
import ts from "../../assets/ts.png";
import reactjs from "../../assets/reactjs.png";
import styledComponents from "../../assets/StyledComponents.svg";
import redux from "../../assets/Redux.svg";
import figma from "assets/Figma-Dark.svg";
import vscode from "assets/VSCode-Dark.svg";
import git from "assets/Git.svg";
import postman from "assets/Postman.svg";
import aws from "assets/AWS-Dark.svg";
import firebase from "assets/Firebase-Dark.svg";
import { useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import useIntersectionObserver from "hooks/useIntersectionObserver";

const Stack = () => {
  // frontendData 
  const frontendData = [
    {
      image: html5,
      title: "HTML5",
      content:
        "  &       .",
    },
    {
      image: css3,
      title: "CSS3",
      content:
        "flexbox, grid         .",
    },
    {
      image: js,
      title: "Javascript",
      content:
        "ES6+  ,     .",
    },
    {
      image: ts,
      title: "Typescript",
      content:
        "/  ,       .",
    },
    {
      image: reactjs,
      title: "React",
      content:
        "   hooks ,    .",
    },
    {
      image: styledComponents,
      title: "Styled-components",
      content:
        "CSS-in-JS   ,      .",
    },
    {
      image: redux,
      title: "Redux-toolkit",
      content:
        "store & slice  , redux-persist        .",
    },
  ];

  // toolsData 
  const toolsData = [
    {
      image: figma,
      title: "Figma",
      content: "UI/UX      .",
    },
    {
      image: vscode,
      title: "VS Code",
      content:
        "   ,      .",
    },
    {
      image: git,
      title: "Git",
      content:
        "Github  ,  &        .",
    },
    {
      image: postman,
      title: "Postman",
      content: "REST API      .",
    },
  ];

  // devOpsData 
  const devOpsData = [
    {
      image: aws,
      title: "AWS",
      content:
        "EC2   , S3, cloudfront, ACM  HTTPS  .",
    },
    {
      image: firebase,
      title: "Firebase",
      content:
        "serverless  , storage    .",
    },
  ];

  const target = useRef(null);
  const navigate = useNavigate();

  const [observe, unobserve] = useIntersectionObserver(() => {
    navigate("/#2");
  });

  useEffect(() => {
    if (target.current !== null && target.current !== undefined) {
      observe(target.current);
    }

    return () => {
      if (target.current !== null && target.current !== undefined) {
        unobserve(target.current);
      }
    };
  }, []);

  return (
    <Container id="2" ref={target}>
      <StackWrapper>
        <SectionTitle>
          <h3>Tech Stack</h3>
        </SectionTitle>
        <p className="title_detail">
               .
        </p>
        <StackLists name="# FrontEnd" values={frontendData} />
        <StackLists name="# Tools" values={toolsData} />
        <StackLists name="# DevOps" values={devOpsData} />
      </StackWrapper>
    </Container>
  );
};

export default Stack;

const Container = styled.section`
  width: 100%;
  padding: 8rem 0;
  background-color: rgb(21, 24, 27);
`;

const StackWrapper = styled.div`
  max-width: 95rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;

  .title_detail {
    font-size: 1.4rem;
    font-weight: 500;
    color: rgb(255, 255, 255);
    margin-bottom: 4rem;

    @media (max-width: 1060px) {
      display: none;
    }
  }
`;

const SectionTitle = styled.div`
  display: table;
  font-size: 3.8rem;
  font-weight: 500;
  border-bottom: 2px solid #cccccc;
  color: white;
  padding-bottom: 1.5rem;
  margin: 0 auto 4.5rem;
  letter-spacing: 4px;
`;
